import { fetchCast, fetchGenre, fetchMovieDetail } from "@/lib/fetchData";
import { MovieType } from "@/types/global";
import Link from "next/link";

export default async function Movie({
  params,
}: {
  params: Promise<MovieType>;
}) {
  const { id } = await params;
  const movie = await fetchMovieDetail(id);
  const genreByMovie = movie.genres;
  const cast = await fetchCast(id);

  const cover = "http://image.tmdb.org/t/p/w1280";
  const profile = "http://image.tmdb.org/t/p/w185";
  const poster = "http://image.tmdb.org/t/p/w185";

  return (
    <div>
      <div>
        <img src={cover + movie.backdrop_path} />
      </div>

      <div>
        <img src={poster + movie.poster_path} />
        <div>
          <h1 className="mb-4 pb-2 text-4xl font-bold">
            {movie.title}{" "}
            <span className="text-2xl font-light">
              ({movie.release_date.split("-")[0]})
            </span>
          </h1>
          <span>
            {genreByMovie.map((item) => {
              return <span key={item.id}>{item.name}</span>;
            })}
          </span>
        </div>
      </div>

      {/* <div className="flex gap-4 flex-wrap">
        {cast.map((person) => {
          return (
            <div key={person.id} className="text-center w-[185px]">
              {person.profile_path ? (
                <img src={profile + person.profile_path} />
              ) : (
                <div className="h-[280px] bg-gray-300"></div>
              )}
              <Link href={`/person/${person.id}`}>
                <b>{person.name}</b>
              </Link>
              <div className="text-sm text-gray-600">{person.character}</div>
            </div>
          );
        })}
      </div> */}
    </div>
  );
}
