import { GenreType, MovieType, PersonType } from "@/types/global";

export async function fetchMovieDetail(id: number): Promise<MovieType> {
  const res = await fetch(`https://api.themoviedb.org/3/movie/${id}`, {
    headers: {
      Authorization: `Bearer ${process.env.TMDB_KEY}`,
    },
  });

  return await res.json();
}

export async function fetchMovieByGenre() {
  const res = await fetch(`https://api.themoviedb.org/3/movie`, {
    headers: {
      Authorization: `Bearer ${process.env.TMDB_KEY}`,
    },
  });

  return await res.json();
}

export async function fetchCast(id: number): Promise<PersonType[]> {
  const res = await fetch(`https://api.themoviedb.org/3/movie/${id}/credits`, {
    headers: {
      Authorization: `Bearer ${process.env.TMDB_KEY}`,
    },
  });
  const result = await res.json();
  return result.cast;
}

export async function fetchGenre(id: number): Promise<GenreType[]> {
  const res = await fetch(
    `https://api.themoviedb.org/3/discover/movie?with_genres=${id}`,
    {
      headers: {
        Authorization: `Bearer ${process.env.TMDB_KEY}`,
      },
    }
  );

  const data = await res.json();
  return data.results;
}
